"use client";

import Link from "next/link";
import { usePathname } from "next/navigation";
import styles from "./sidebar.module.css";

const navItems = [
  { href: "/", label: "Home", icon: "üè†" },
  { href: "/templates", label: "Templates", icon: "üé¨" },
  { href: "/projects", label: "Projects", icon: "üìÅ" },
  { href: "/batch", label: "Batch Jobs", icon: "‚ö°" },
  { href: "/settings", label: "Settings", icon: "‚öôÔ∏è" },
];

export function Sidebar() {
  const pathname = usePathname();

  return (
    <aside className={styles.sidebar}>
      <div className={styles.logo}>
        <span className={styles.logoIcon}>üé¨</span>
        <span className={styles.logoText}>ClipFactory</span>
      </div>

      <nav className={styles.nav}>
        {navItems.map((item) => (
          <Link
            key={item.href}
            href={item.href}
            className={`${styles.navItem} ${
              pathname === item.href ? styles.active : ""
            }`}
          >
            <span className={styles.navIcon}>{item.icon}</span>
            <span className={styles.navLabel}>{item.label}</span>
          </Link>
        ))}
      </nav>

      <div className={styles.footer}>
        <button className={styles.openEditorBtn}>
          <span>üöÄ</span>
          <span>Open OpenCut</span>
        </button>
      </div>
    </aside>
  );
}
